@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.search-box{display:flex;flex-direction:row;align-items:center}.search-box input{font-size:1rem;padding:1rem 3rem 1rem 2rem;border:none;box-shadow:0 0.1875rem 0.375rem var(--color-shadow);width:25ch;border-radius:1.25rem;color:var(--color-primary)}.search-box input::-webkit-input-placeholder{color:var(--color-primary)}.search-box input:-ms-input-placeholder{color:var(--color-primary)}.search-box input::-ms-input-placeholder{color:var(--color-primary)}.search-box input::placeholder{color:var(--color-primary)}.search-box .search{margin-left:-2.5rem;margin-right:1rem;-webkit-transform:rotateY(180deg);transform:rotateY(180deg);fill:var(--color-primary);pointer-events:none}@media screen and (max-width: 45rem){.search-box input{width:calc(100% + 1rem)}.search-box .search{margin-right:0}}

.option-checkbox{display:flex;flex-direction:row;align-items:center;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.option-checkbox .input-box{width:1.875em;height:1.875em;border-radius:0.625rem;box-shadow:0 0.1875rem 0.375rem var(--color-shadow);margin-right:0.6rem}.option-checkbox .input-box .check{fill:var(--color-primary)}.option-checkbox .label{font-size:1rem;color:var(--color-text-medium)}

.add-button{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:none;border-radius:1.2rem;font-size:1rem;padding:1rem 1.5rem;background-color:var(--color-primary);box-shadow:0 0.1875rem 0.375rem var(--color-shadow);display:flex;flex-direction:row;align-items:center;justify-content:center}.add-button:hover{cursor:pointer}.add-button .plus{margin-right:0.3rem;fill:var(--color-text-button)}.add-button span{font-size:1rem;color:var(--color-text-button)}

.list-controls{display:flex;flex-direction:row;align-items:center;justify-content:space-between;margin:3rem 0}.list-controls .left{display:flex;flex-direction:row;align-items:center;justify-content:center}.list-controls .left>*:first-child{margin-right:1rem}@media screen and (max-width: 45rem){.list-controls{flex-direction:column;align-items:stretch}.list-controls .left{flex-direction:column;align-items:stretch}.list-controls .left>*:last-child{margin:1rem 0}.list-controls .right>*{margin-top:1rem}}

.book-card{background-color:var(--color-card-background);box-shadow:0 0.1875rem 0.375rem var(--color-shadow);border-radius:1.25rem;margin:1rem 0;padding:2rem 1rem}.book-card .top{display:flex;flex-direction:row;justify-content:space-between}.book-card .top .left h1{font-size:1.5rem;color:var(--color-text-dark);font-weight:bold}.book-card .top .left .details{font-size:1rem;color:var(--color-text-light)}.book-card .top .left .details .author-page-separator{margin:0 1ch;display:inline-block}.book-card .top .right{display:flex;flex-direction:row;align-items:flex-start}.book-card .top .right button{border:none;font-size:1rem;color:var(--color-primary);text-decoration:underline;background-color:inherit;cursor:pointer}.book-card .description{font-size:1.5rem;color:var(--color-text-dark);margin:1.5rem 0}.book-card ul.tags li{display:inline-block;list-style-type:none;font-size:1.5rem;color:var(--color-primary);margin-right:1ch}

.book-register-modal{position:fixed;top:0;right:0;bottom:0;left:0;background-color:var(--color-modal-background);padding:3rem 2rem;overflow:auto;dispaly:flex}.book-register-modal form{margin:0 auto;max-width:42rem;width:100%;padding:2rem;background-color:var(--color-card-background);border-radius:1.25rem}.book-register-modal form header{display:flex;flex-direction:row;align-items:center}.book-register-modal form header span{font-size:1.5rem;color:var(--color-primary);margin-left:1ch}.book-register-modal form header .plus{fill:var(--color-primary)}.book-register-modal form .fields{display:flex;flex-direction:column;padding:3rem 4rem}.book-register-modal form .fields label{font-size:1.5rem;color:var(--color-text-dark);margin-bottom:0.25rem;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.book-register-modal form .fields input{font-size:1rem;color:var(--color-text-dark);padding:0.93rem;margin-bottom:1.25rem;border-radius:0.625rem;border:1px solid var(--color-primary)}.book-register-modal form footer{padding:0 4rem;display:flex;flex-direction:row;align-items:center;justify-content:space-between}.book-register-modal form footer button{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:none;border-radius:1.2rem;font-size:1rem;line-height:1.5;padding:1rem 2rem;background-color:var(--color-primary);box-shadow:0 0.1875rem 0.375rem var(--color-shadow);color:var(--color-text-button);cursor:pointer;display:flex;flex-direction:row;align-items:center;justify-content:center}.book-register-modal form footer button span{margin-left:1ch}@media screen and (max-width: 45rem){.book-register-modal{padding:2rem 1rem}.book-register-modal form{padding:2rem 1rem}.book-register-modal form .fields{padding:2rem 0}.book-register-modal form footer{padding:0}.book-register-modal form footer button{padding:0.8rem 1.2rem;border-radius:1rem}}

.home{display:flex;flex-direction:column;align-items:center;padding:3rem 2rem}.home .container{max-width:var(--dimensions-max-width);width:100%}.home .container h1{font-size:3.75rem;font-weight:normal;margin-bottom:0.4375rem;color:var(--color-text-dark)}.home .container h2{font-size:2.5rem;font-weight:normal;color:var(--color-text-medium)}

*{margin:0;padding:0;box-sizing:border-box;font-family:'Roboto', sans-serif;outline:none}html{height:100%;font-size:medium}body{min-height:100%;background-color:var(--color-page-background);--color-page-background: #fefefe;--color-card-background: #FFFFFF;--color-modal-background: rgb(139, 139, 139, 0.49);--color-text-button: #FFFFFF;--color-shadow: #00000029;--color-text-dark: #707070;--color-text-medium: #A8A8B3;--color-text-light: #BFBFBF;--color-primary: #9466FF;--dimensions-max-width: 935px}

